import React from 'react';
import emailjs from 'emailjs-com';

class Kontakt extends React.Component {
  state = {
    user_name: '',
    email: '',
    msg: '',
    nameIsOkay: false,
    emailIsOkay: false,
    msgIsOkay: false,
    sendMsg: false,

  }
  sendEmail = (e) => {
    e.preventDefault();
    if (!this.state.sendMsg) return

    emailjs.sendForm("dietacud.pl@gmail.com", "template_nppa8wj", e.target, 'user_iQFi1eTP3SxSGEewhhpe4')
      .then((result) => {
        console.log(result.text);
      }, (error) => {
        console.log(error.text);
      });
    e.target.reset();
    this.resetForm();

  }
  resetForm = () => {
    this.setState({
      user_name: '',
      email: '',
      msg: '',
    })
  }

  checkInputs = () => {
    const { user_name, email, msg, nameIsOkay, emailIsOkay, msgIsOkay } = this.state
    if (user_name.trim().length > 2) this.setState({ nameIsOkay: true })
    else { this.setState({ nameIsOkay: false }) }

    if (email.trim().length > 2) this.setState({ emailIsOkay: true })
    else { this.setState({ emailIsOkay: false }) }

    if (msg.trim().length > 4) this.setState({ msgIsOkay: true })
    else { this.setState({ msgIsOkay: false }) }

    if (nameIsOkay && msgIsOkay && emailIsOkay) this.setState({ sendMsg: true })
    else { this.setState({ sendMsg: false }) }
  }

  handleChange = (fieldName, value) => {
    this.setState({ [fieldName]: value }, this.checkInputs)
  }
  render() {
    const { nameIsOkay, emailIsOkay, msgIsOkay } = this.state


    return (
      <div className="kontakt-container">
        <form className="contact-form" onSubmit={this.sendEmail}>
          <label>Name</label>
          <div><input type="text" name="user_name"
            value={this.state.name}
            onChange={(e) => this.handleChange("user_name", e.target.value)} />
            <div className="isOK "
              style={nameIsOkay ? { backgroundColor: 'green' } : { backgroundColor: 'red' }}
            >X</div></div><label>Email</label>
          <div><input type="email" name="user_email"
            value={this.state.email}
            onChange={(e) => this.handleChange("email", e.target.value)} />
            <div className="isOK"
              style={emailIsOkay ? { backgroundColor: 'green' } : { backgroundColor: 'red' }}
            >X</div></div><label>Message</label>
          <div><textarea name="message"
            value={this.state.msg}
            onChange={(e) => this.handleChange("msg", e.target.value)} />
            <div className="isOK"
              style={msgIsOkay ? { backgroundColor: 'green' } : { backgroundColor: 'red' }}
            >X</div></div>
          <input type="submit" value="Send" />
        </form>
      </div>
    );
  }
}

export default Kontakt;
